:root {
    --primary: #44AF69;
    --secondary: #14342B;
    --tertiary: #E3C567;
    --quartiary: #7D451B;
    --light: #fafafa;
    --dark: #272f32;
    --error: rgb(228, 46, 1);
    --spacing-one: calc(1rem * 0.5);
    --spacing-two: calc(1rem);
  }
  
  * {
    box-sizing: border-box;
  }
  
  html,
  body {
    min-height: 100%;
  }
  
  body {
    margin: 0;
    padding: 0;
    font-family: 'Mukta Vaani', sans-serif;
    font-weight: 400;
    line-height: 1.5;
    font-size: 16px;
    color: var(--dark);
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-family: 'Mukta Vaani', sans-serif;
    font-weight: 700;
    margin: 5px 0;
    line-height: 1.25;
  }
  
  h1 {
    font-size: 2.25rem;
  }
  
  h2 {
    font-size: 2rem;
  }
  
  h3 {
    font-size: 1.75rem;
  }
  
  h4 {
    font-size: 1.5rem;
  }
  
  h5 {
    font-size: 1.25rem;
  }
  
  h6 {
    font-size: 1rem;
  }
  
  p {
    font-size: 1.1rem;
    margin-top: 0;
  }
  
  a {
    color: var(--tertiary);
    text-decoration: none;
    font-weight: bold;
  }
  
  a:hover {
    text-decoration: underline;
  }
  
  header {
    background: var(--primary);
  }
  
  header a {
    color: var(--light);
  }

  header ul {
    list-style: none;
    font-size: 18pt;
    margin: auto 0;
  }

  nav {
    margin: auto 0;
  }

  .brand {
    font-size: 36pt;
    font-family: 'Anton', sans-serif;
    letter-spacing: .2rem;
    font-weight: 400;
    margin: auto 0;
  }

  .mainlinks {
    display: flex;
    width: 100%;
    height: 6rem;
  }

  .sections {
    display: flex;
    justify-content: space-evenly;
    /* width: 100vw; */
    height: 60px;
    margin: 0px;
    background: var(--secondary);
  }

  .labels {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
  }
  
  button {
    font-size: 14pt;
    text-decoration: none;
    padding: 0.25rem 1rem;
    border: none;
    outline: none;
    cursor: pointer;
    margin: auto 0;
    color: var(--light);
    background: none;
  }
  
  button:hover {
    opacity: .8;
  }
  
  button[disabled] {
    cursor: default;
    opacity: .8;
  }
  
  img {
    max-width: 100%;
  }
  
  span[role="img"] {
    cursor: default;
  }
  
  form {
    width: 40%;
  }
  
  input {
    font-size: 1rem;
    padding: 0.25rem;
    border: 1px solid var(--dark);
    outline: none;
    border-radius: 3px;
    transition: box-shadow 0.2s;
  }
  
  input:focus {
    border: 1px solid var(--secondary);
    box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
  }
  
  .container {
    width: 85%;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
  }
  
  .flex-row {
    display: flex;
    flex-wrap: wrap;
  }
  
  .space-between {
    justify-content: space-between;
  }
  
  .flex-end {
    justify-content: flex-end;
  }
  
  .mx-1 {
    margin-right: var(--spacing-one);
    margin-left: var(--spacing-one);
  }
  
  .mx-2 {
    margin-right: var(--spacing-two);
    margin-left: var(--spacing-two);
  }
  
  .my-1 {
    margin-top: var(--spacing-one);
    margin-bottom: var(--spacing-one);
  }
  
  .my-2 {
    margin-top: var(--spacing-two);
    margin-bottom: var(--spacing-two);
  }

  .my-5 {
    margin-top: 3em;
    margin-bottom: 3em;
  }
  
  .px-1 {
    padding-right: var(--spacing-one);
    padding-left: var(--spacing-one);
  }
  
  .px-2 {
    padding-right: var(--spacing-two);
    padding-left: var(--spacing-two);
  }

  .px-5{
    padding-right: 3em;
    padding-left: 3em;
  }

  .pl-5{
    padding-left: 3em;
  }

  .pr-3{
    padding-right: 1em;
  }
  
  .py-1 {
    padding-top: var(--spacing-one);
    padding-bottom: var(--spacing-one);
  }
  
  .py-2 {
    padding-top: var(--spacing-two);
    padding-bottom: var(--spacing-two);
  }
  
  .text-center{
    text-align: center;
  }

  .card {
    /* width: 25%; */
    text-align: center;
  }
  
  .error-text {
    color: var(--error);
  }
  
  @media screen and (max-width: 768px) {
    .card {
      width: 100%;
    }
  }

.product-list-image{
  width: 100%;
  height: 220px;
  object-fit: cover;
}

/* Slider settings */
.slick-prev{
  left: -77px;
  display: block !important;
  fill: var(--secondary) !important;
  background-color: #ebebeb;
  border-radius: 60px 0px 0px 60px;
  width: 90px !important;
  height: 120px !important;
}

.slick-next{
  right: -77px;
  display: block !important;
  fill: var(--secondary) !important;
  background-color: #ebebeb;
  border-radius: 0px 60px 60px 0px;
  width: 90px !important;
  height: 120px !important;
}

.slick-next:hover, .slick-prev:hover{
  background-color: #ebebeb;
  fill: var(--primary) !important;
}

.slick-slider {
  background-color: #ebebeb;
  padding: 1em;
}

.pCard-info {
  background-color: var(--secondary);
  color: var(--light);
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  padding: 5px 20px 10px;
  height: 160px;
  overflow: hidden;
}

.pCard-info h4{
  text-align: center;
}

.pCard-info-add{
  display: flex;
  align-items: flex-start;
  margin-top: 4px;
  justify-content: space-evenly;
}